.Orphanage {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin-left: 1.5rem;
}

.Orphanage__wrapper {
  flex: 1;
}

.Orphanage__actions {
  display: flex;
  flex: 0 1 auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
}

.Orphanage__select-space {
  width: 90%;
  margin: 1rem;
}

.Orphanage__title {
  text-transform: uppercase;
  justify-content: center;
}
